type Filter = Partial<{
  root: boolean;
  gameOver: boolean;
  scoreContainer: boolean;
  scoreLabel: boolean;
  scorePoints: boolean;
  replayButton: boolean;
  replayLabel: boolean;
}>;

class GameOverLayout {
  public readonly root: PIXI.Container;
  public readonly gameOver: PIXI.Text;
  public readonly scoreContainer: PIXI.Container;
  public readonly scoreLabel: PIXI.Text;
  public readonly scorePoints: PIXI.Text;
  public readonly replayButton: PIXI.Sprite;
  public readonly replayLabel: PIXI.Text;

  constructor(filter?: Filter) {
    populate(this, raw, filter);
  }
}

