import * as PIXI from 'pixi.js';
import { populate } from 'populate';

const raw = {
  list : [
  ],
  x: 0,
	y: 0,
	width: 420,
	height: 800,
	handleResize: true
}

type Filter = Partial<{
  root: boolean;
  shine: boolean;
  mainImage: boolean;
  header: boolean;
  description: boolean;
  adButton: boolean;
  closeButton: boolean;
}>;

class CommonPopupLayout {
  public readonly root: PIXI.Container;
  public readonly shine: PIXI.Sprite;
  public readonly mainImage: PIXI.Sprite;
  public readonly header: PIXI.Text;
  public readonly description: PIXI.Text;
  public readonly adButton: PIXI.Sprite;
  public readonly closeButton: PIXI.Sprite;

  constructor(filter?: Filter) {
    populate(this, raw, filter);
  }
}

export { CommonPopupLayout, Filter, raw };